[data-anim="bounce"].has-animate {
	animation-name: bounce;
	transform-origin: 50% 95%;
}

@keyframes bounce {
	0% {
		transform: scaleY(0.95);
	}
	25% {
		transform: scaleY(1) scaleX(0.95);
	}
	50% {
		transform: scaleY(0.95);
	}
	75% {
		transform: scaleY(1) scaleX(0.95);
	}
	100% {
		transform: scaleY(0.95);
	}
}

[data-anim="bounce2"].has-animate {
	animation-name: bounce2;
	transform-origin: 50% 95%;
